.head-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 60px;
}

.head-image{
    width: auto;
    transition: max-width 1s, min-width 1s;

}

.head-image-expanded{
    min-width: 230ox;
    max-width: 250px;

}

.head-image-contracred{
    min-width: 40px;
    max-width: 60px;

}

.head-text{
    font-family: 'Hachi Maru Pop', cursive;
    margin: 5px 0 5px 0;
    transition: font-size 1s;
}

.head-text-expanded{
    font-size: 36px;
}

.head-text-contracted{
    font-size: 12px;

}